@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

body {
    margin: 0;
    padding: 0;
    font-family: "Montserrat", sans-serif;
    background-color: #EF2A39;
    color: #EF2A39;
    text-align: center;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 5rem;
    background-color: #fff;
}

.logo {
    font-size: 3rem;
    font-family: "Lobster", sans-serif;
}

.logo span {
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    font-size: 1.125rem;
    display: block;
}

nav {
    display: flex;
    gap: 3rem;
}

nav a {
    text-decoration: none;
    color: #EF2A39;
    font-size: 1.5rem;
    font-weight: bold;
}

.social-media {
    display: flex;
    gap: 0.625rem;
}

.social-media img {
    width: 2.5rem;
    height: 2.5rem;
}

.welcome-section {
    display: flex;
    justify-content: space-between;
    padding: 5rem 10% 5rem 5%;
    background-color: #fff;
}

.welcome-text {
    max-width: 50%;
    text-align: left;
}

.welcome-text h1 {
    margin: 0;
    font-size: 4rem;
    font-weight: bold;
    margin-top: 5rem;
    margin-bottom: 2.5rem;
}

.welcome-text p {
    font-size: 1.25rem;
    color: #3C2F2F;
    margin-bottom: 9.375rem;
}

.welcome-buttons {
    flex-direction: column;
    display: flex;
    max-width: 15rem;
}

.welcome-buttons a {
    display: inline-block;
    padding: 1.125rem 1.875rem;
    font-size: 1.25rem;
    font-weight: 600;
    border-radius: 1.5625rem;
    text-decoration: none;
    text-align: center;
}

.btn-primary {
    background-color: #EF2A39;
    color: white;
    margin-bottom: 1.875rem;
}

.btn-secondary {
    background-color: #000000;
    color: white;
}

.burger-image {
    max-width: 40%;
}

.burger-image img {
    width: 100%;
    height: auto;
}

.about-section {
    background: url('./img/about-background.png') no-repeat center center/cover;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #fff;
    position: relative;
}

.about-content {
    background: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
}

.about-title {
    font-family: "Lobster", sans-serif;
    margin-bottom: 8.125rem;
    margin-top: 3.125rem;
    font-size: 8rem;
}

.subtitle {
    display: block;
    font-size: 2.5rem;
    font-family: "Montserrat", sans-serif;
}

.about-description {
    font-size: 3.125rem;
    font-weight: 400;
    text-align: justify;
    margin: 1.25rem 17rem;
}

.highlight {
    font-family: "Lobster", sans-serif;
    color: #EF2A39;
    font-weight: bold;
}

.btn {
    color: #fff;
    background-color: #EF2A39;
    border-radius: 3.125rem;
    margin: 8.25rem 0;
    padding: 1.875rem 2.1875rem;
    font-weight: 600;
    font-size: 2.25rem;
    text-decoration: none;
    transition: background-color .3s;
    display: inline-block;
}

.popular-dishes-section, .reviews-section {
    padding: 8.125rem 5rem 13.75rem;
    background-color: #EF2A39;
    color: white;
    text-align: center;
}

section h2 {
    margin-top: 0;
    margin-bottom: 1.875rem;
    font-size: 5rem;
}

section p {
    margin-top: 0;
    margin-bottom: 8.125rem;
    font-size: 1.5rem;
}

.dishes-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
}

.dish-item {
    background-color: white;
    padding: 1.25rem;
    border-radius: 0.9375rem;
    color: #333;
    text-align: center;
}

.dish-item h3 {
    margin: 0.625rem 0;
    text-align: left;
    font-size: 1.75rem;
}

.dish-item h3 a {
    text-decoration: none;
    color: #3C2F2F;
}

.dish-item img {
    width: 11.8125rem;
    height: 12.5rem;
}

.dish-item p {
    margin-top: 0;
    margin-bottom: 3.125rem;
    font-size: 1rem;
    text-align: left;
}

.dish-item .hamb {
    width: 13.5rem;
    height: 10rem;
    margin-top: 2.5rem;
}

.dish-info {
    display: flex;
    justify-content: space-between;
}

.dish-item .price {
    font-size: 1.75rem;
    color: #EF2A39;
    font-weight: bold;
}

.dish-item .rating {
    color: #FF9633;
    font-weight: bold;
    font-size: 1.75rem;
}

.reviews-section p {
    margin-bottom: 3.125rem;
}

.reviews-item {
    background-color: white;
    padding: 2.5rem;
    border-radius: 1.5625rem;
    display: inline-block;
    color: #333;
}

.reviews-item .customer-photo {
    border-radius: 50%;
    width: 9.375rem;
    height: 9.375rem;
    object-fit: cover;
}

.reviews-item h3 {
    font-size: 2.5rem;
    margin-top: 1.875rem;
    margin-bottom: 3.75rem;
}

.reviews-item p {
    max-width: 37.5rem;
    font-size: 1.875rem;
    margin-bottom: 3.75rem;
}

.rating {
    display: flex;
    gap: 1.25rem;
    justify-content: center;
}

.rating img {
    width: 2.1875rem;
    height: 2.1875rem;
}

.reviews-navigation {
    margin-top: 5.625rem;
    display: flex;
    gap: 3.75rem;
    justify-content: center;
}

.dot {
    height: 1.5625rem;
    width: 1.5625rem;
    background-color: #333;
    border-radius: 50%;
    display: inline-block;
}

.active {
    background-color: #fff;
}

footer {
    background: url('./img/footer-background.png') no-repeat center center/cover;
    height: 100vh;
    color: white;
    text-align: center;
    position: relative;
}

.footer-content {
    background: rgba(0, 0, 0, 0.75);
    height: 100%;
    width: 100%;
}

.footer-title {
    background-color: #EF2A39;
}

.footer-logo {
    padding-top: 3.125rem;
    padding-bottom: 2.5rem;
    font-family: Lobster, sans-serif;
    font-size: 8rem;
}

.footer-info {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 5rem;
    padding: 0 5rem;
    text-align: left;
}

.footer-info > div {
    flex: 1;
    min-width: 9.375rem;
    margin: 0.625rem 1.25rem;
}

.footer-info h3 {
    font-size: 1.5rem;
    margin-bottom: 0.625rem;
    color: #EF2A39;
    text-transform: uppercase;
}

.footer-info p {
    font-size: 1.25rem;
    color: #FFFFFF;
}

.footer-info a {
    color: #f5f5f5;
    text-decoration: none;
}

.footer-bottom {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-top: 1px solid #EF2A39;
    padding-top: 2.1875rem;
    font-size: 1.25rem;
    margin: 0 5rem;
    color: #fff;
}

.footer-bottom .terms {
    display: flex;
    align-items: center; 
    gap: 1.875rem; 
    margin-bottom: 2.5rem; 
}

.terms a {
    text-decoration: none;
    color: #FFF;
}
.booking-section {
    margin: 0 5rem;
}

.gofood-header, .gofood-header a {
    background-color: #EF2A39;
    color: #fff;
}

.booking-section h1 {
    color: #fff;
    font-size: 5rem;
    text-transform: uppercase;
    margin: 0;
    padding-top: 3.125rem;
    padding-bottom: 6.25rem;
}

form {
    display: flex;
    justify-content: space-between;
    color: #fff;
    padding: 0;
    gap: 8.75rem;
    margin: 0 10rem;
}

.form-column {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.form-item {
    max-width: 25rem;
}

.form-item h2 {
    font-size: 2rem;
    text-align: left;
}

.form-group {
    margin-bottom: 0.9375rem;
}

input[type="text"],
input[type="tel"],
input[type="email"],
input[type="date"],
input[type="time"],
select,
textarea {
    width: 95%;
    padding: 0.796875rem;
    border: 1px solid #ccc; 
    border-radius: 0.3125rem; 
    font-size: 1.25rem;
    color: #808080;
    font-family: "Montserrat", sans-serif;
}

.form-select {
    text-align: left;
}

.form-select select {
    width: 102%;
}

textarea {
    height: 9.375rem;
    resize: none;
}

.form-input {
    background-position: 23.125rem 0.625rem;
    background-repeat: no-repeat;
    padding-left: 0.9375rem;
}

select, input {
    appearance: none;
}

.location {
    background-image: url('./img/icon/location.svg');
}

.persons {
    background-image: url('./img/icon/person.svg');
}

.btn-reserve {
    margin-top: auto;
    padding: 1.25rem 3.75rem;
    background-color: #000000;
    color: white;
    border: none;
    border-radius: 1.25rem; 
    font-size: 1.25rem;
    cursor: pointer;
}

.search-filter .filter-input {
    background-position: 1.25rem 1.25rem;
    background-repeat: no-repeat;
    font-size: 1.5rem;
    border-radius: 1.25rem;
    padding: 0.9375rem 0 0.9375rem 3.75rem;
}

.search {
    background-image: url('./img/icon/serach.svg');
}

.menu-section {
    margin: 0 5rem;
}

.menu-section h1 {
    color: #fff;
    font-size: 5rem; 
    text-transform: uppercase;
    margin: 0;
}

.menu-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 3.125rem;
}

.search-filter {
    display: flex;
    align-items: center;
    gap: 0.9375rem;
}

.filter-btn {
    background-color: #000000;
    border-radius: 1.25rem;
    padding: 0.9375rem;
}

.categories {
    display: flex;
    justify-content: space-between; 
    flex-wrap: wrap;
    padding: 4.375rem 0; 
}

.category-btn {
    width: 10rem;
    height: 5rem; 
    background-color: #fff;
    color: #EF2A39;
    border: none;
    border-radius: 1.875rem;
    font-size: 1.5rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
}

.marked {
    width: 5.625rem; 
    background-color: #000000;
    color: #fff;
}

.pagination-dots {
    display: flex;
    justify-content: center;
    padding: 6.25rem 0; 
    gap: 1.875rem;
}

.pagination-dot { 
    width: 5rem; 
    height: 5rem; 
    border-radius: 3.125rem; 
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 2.25rem; 
    font-weight: bold;
    text-decoration: none;
}

.activated {
    background-color: #fff;
    color: #000000;
}

.menu-item {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 3.125rem;
    background-color: #fff;
    padding: 0.625rem 5rem 0;
}

.back-btn {
    background: none;
    border: none;
    cursor: pointer;
}

.dish-details {
    display: flex;
    align-items: flex-start;
    gap: 3.75rem;
    width: 100%;
    margin-bottom: 9.375rem; 
}

.dish-image {
    width: 37.5rem;
    height: auto;
}

.dish-inform {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.dish-inform h1 {
    font-size: 4rem;
    line-height: 1.2;
    color: #3C2F2F;
    text-align: left;
    margin: 0;
    padding: 1.875rem 0;
}

.dish-meta {
    display: flex;
    align-items: center;
    gap: 2.5rem;
    font-size: 1.75rem;
    color: #808080;
    font-weight: 600;
}

.dish-meta .rating {
    color: #FF9633;
}

.dish-desc {
    font-size: 1.25rem;
    line-height: 1.5;
    color: #808080;
    text-align: justify;
    margin: 2.5rem 0;
}

.price-book {
    display: flex;
    align-items: center;
    gap: 2.5rem; 
    height: 5rem; 
}

.price-btn {
    flex: 1;
    background-color: #EF2A39;
    color: #fff;
    padding: 1.25rem 1.875rem; 
    border-radius: 3.125rem;
    font-size: 2rem; 
    font-weight: bold;
}

.book-btn {
    flex: 2;
    text-decoration: none;
    background-color: #333;
    color: #fff;
    border: none;
    padding: 1.25rem 1.875rem; 
    border-radius: 1.875rem;
    cursor: pointer;
    font-size: 2rem; 
    font-weight: bold;
}